import React from "react";
import NavbarPage from "../Navbar/NavBarPage";
import {
  Profile,
 


  Education,
  Portfolio,
} from "../Profile.js";
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";

const Layout = () => {
  return (
    <MDBContainer>
      <div>
        <NavbarPage />

        <MDBCol md="12">
          <MDBRow>
            <MDBCol md="4" className="fixxx">
              <Profile />
            </MDBCol>
            <MDBCol md="8">
              <Portfolio />
            </MDBCol>
          </MDBRow>

          <MDBRow>
            <MDBCol md="4">
            
           
            </MDBCol>
          
          </MDBRow>
        </MDBCol>
      </div>
    </MDBContainer>
  );
};

export default Layout;
