 class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
      <form
        onSubmit={this.submitForm}
        action="https://formspree.io/mzbjpnrv"
        method="POST"
      >
       


        <p className="h4 text-center mb-4">Send me an Email</p>
        <label name="email" type="email" htmlFor="defaultFormLoginEmailEx" className="grey-text">
          Your Email
        </label>
        <input type="email" id="defaultFormLoginEmailEx" className="form-control" />
        <br />
        
        <label htmlFor="defaultFormContactMessageEx" className="grey-text">
          Your message
        </label>
        <textarea type="text" name="message" id="defaultFormContactMessageEx" className="form-control" rows="3" />
        <div className="text-center mt-4">
        

        
        

        {status === "SUCCESS" ? <MDBBadge color="success"><h5>Thank you for your Email! I'll reply to you as soon as possible.</h5></MDBBadge> : <button className="btn">Submit</button>}
        {status === "ERROR" && <p>Ooops! There was an error.</p>}
        </div>
      </form>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}

